import { React } from 'react';
import { Link } from 'react-router-dom'; 
import './MatchSmallCard.scss';

export const MatchSmallCard = ( {match,teamName} ) => {
  if(!match){
    return (
      <div className="MatchSmallCard">
      <p>No Mathes found</p>
    </div>
    );
  }

  const oponentTeam = teamName===match.team1 ? match.team2 : match.team1;
  const oponentTeamRoute = `/teams/${oponentTeam}`;
  const isWinner = teamName.trim() === match.winner.trim();
  return (
    <div className={isWinner ? 'MatchSmallCard win-card' : 'MatchSmallCard lost-card'}>
      <span className="vs-section">Vs</span>
      <h3> <Link to={oponentTeamRoute}>{oponentTeam}</Link></h3>
      <p className="result-section">{match.winner} won by {match.resultMargin} {match.result}</p>
    </div>
  );
}

