import { React } from 'react';
import { Link } from 'react-router-dom';
import './MatchDetailsCard.scss';


export const MatchDetailsCard = ( {match,teamName} ) => {
  if(!match || !match.team1 || !match.team2 ){
    return (<div className="MatchDetailsCard"><h2>No Matches.</h2></div>);
  }
  const oponentTeam = teamName===match.team1 ? match.team2 : match.team1;
  const oponentTeamRoute = `/teams/${oponentTeam}`;
  const isWinner = teamName.trim() === match.winner.trim();
  console.log(isWinner);
  return (
    <div className={isWinner ? 'MatchDetailsCard win-card' : 'MatchDetailsCard lost-card'}>
      
      <div>
        <span className="vs-section">Vs</span>
        <h1> <Link to={oponentTeamRoute}>{oponentTeam}</Link></h1>
        <h2 className="date-section">{match.date}</h2>
        <h3 className="venue-section">@ {match.venue}</h3>
        <h3 className="result-section">{match.winner} won by {match.resultMargin} {match.result}</h3>
      </div>
      <div className="innings-section">
        <h3>First Innings</h3>
        <p>{match.team1}</p>
        <h3>Second Innings</h3>
        <p>{match.team2}</p>
        <h3>Man of the match</h3>
        <p>{match.playerOfMatch}</p>
        <h3>Umpires</h3>
        <p>{match.umpire1} , {match.umpire2}</p>
      </div>
    </div>
  );
}

