import { React , useEffect , useState } from 'react';
import { useParams } from 'react-router-dom';
import { MatchDetailsCard } from '../components/MatchDetailsCard';
import { YearSelector } from '../components/YearSelector';
import './MatchPage.scss';


export const MatchPage = () => {

    const [matches, setMatches] = useState([]);
    const { teamName, year } = useParams();
    
    useEffect(
        () =>{
            const fetchMatchesOfAGivenTeamForAYear = async ()=>{
                const response = await fetch(`${process.env.REACT_APP_API_ROOT_URL}/teams/${teamName}/matches?year=${year}`); //http://localhost:8080/teams/${teamName}/matches?year=${year}--removing localhost dependency
                const data = await response.json();
                setMatches(data);
                console.log(data);
            }
            fetchMatchesOfAGivenTeamForAYear();
        }, [teamName, year]
    );

  return (
    <div className="MatchPage">
        <div className="year-selector">
            <h3>Select Year </h3>
            <YearSelector teamName={teamName}/>
        </div>
        <div className="all-matches-for-year">
            <h2>{teamName} IPL matches of {year}</h2>
            {               
                matches.map(everyMatch=><MatchDetailsCard key={everyMatch.id} match={everyMatch} teamName={teamName}/>)                
            }
        </div>
    </div>
  );
}

