import { React , useEffect , useState } from 'react';
import { TeamTileCard } from '../components/TeamTileCard';
import './HomePage.scss';

export const HomePage = () => {

    const [teams, setAllTeams] = useState([]);
    
    useEffect(
        () =>{
            const fetchAllTeam = async () =>{
                const response = await fetch(`${process.env.REACT_APP_API_ROOT_URL}/teams`); // http://localhost:8080/teams--removing localhost dependency
                const data = await response.json();
                console.log(data);
                setAllTeams(data);
            }
            fetchAllTeam();
        }, []
    );

  
  return (
    <div className="HomePage">
      <div className="home-header-section">
        <h1 className="app-name-label">IPL Dashboard</h1>
      </div>
      <div className="home-teams-section">
        {
            teams.map(team => <TeamTileCard key={team.id} team={team}/>)
        }
      </div>
     
    </div>
  );
}

