import { React } from 'react';
import './TeamTileCard.scss';
import { Link } from 'react-router-dom';

export const TeamTileCard = ( {team} ) => {

    return(
        <div className="TeamTileCard">
            <h3>
                <Link to={`/teams/${team.teamName}`}> {team.teamName} </Link>
            </h3>
        </div>
    );
}